function getDefaultNumbers() {
  const total = 50000;
  return new Array(total)
    .fill(0)
    .map((it) => Math.floor(Math.random() * total))
    .sort((a, b) => a - b);
}

const IntegerList = memo(({ numbers }) => {
  console.log("render IntegerList");
  return (
    <div>
      {numbers.map((it, idx) => (
        <span
          key={idx}
          style={{
            display: "inline-block",
            textAlign: "center",
            width: "60px"
          }}
        >
          {it}
        </span>
      ))}
    </div>
  );
});

export default function App() {
  const [numbers, setNumbers] = useState(getDefaultNumbers());
  const [showInput, setShowInput] = useState(false);
  const [inputValue, setInputValue] = useState("");
  const slowHandle = () => {
    setShowInput(false);
    setNumbers([...numbers, +inputValue].sort((a, b) => a - b));
  };
  const fastHandle = () => {
    setShowInput(false);
    // å¼‚æ­¥ä¸‹æ¬¡æ“ä½œï¼Œéœ€è¦æ”¾åœ¨ä¸‹æ¬¡å®ä»»åŠ¡ä¸­
    setTimeout(() => {
      setNumbers([...numbers, +inputValue].sort((a, b) => a - b));
    });
  };

  return (
    <div className="App">
      {showInput ? (
        <div>
          <input
            placeholder="è¾“å…¥æ•´æ•°"
            style={{ marginRight: 20 }}
            value={inputValue}
            onChange={(v) => setInputValue(v.target.value)}
          ></input>
          <br />
          <button onClick={slowHandle} style={{ marginTop: 8 }}>
            ç‚¹å‡»æˆ‘æ·»åŠ è¯¥æ•´æ•°ï¼Œé¡µé¢å¡é¡¿ï¼Œä¸ä¼šç«‹å³åé¦ˆç»™ç”¨æˆ·ç»“æœ
          </button>
          <br />
          <button onClick={fastHandle} style={{ marginTop: 8 }}>
            ç‚¹å‡»æˆ‘æ·»åŠ è¯¥æ•´æ•°ï¼Œè¾“å…¥æ¡†ç«‹å³æ¶ˆå¤±ï¼Œç«‹å³åé¦ˆç»™ç”¨æˆ·é¡µé¢å“åº”
          </button>
        </div>
      ) : (
        <button onClick={() => setShowInput(true)}>ç‚¹æˆ‘æ·»åŠ ä¸€ä¸ªæ•´æ•°</button>
      )}
      <IntegerList numbers={numbers}></IntegerList>
    </div>
  );
}
